.App {
  text-align: center;
  color: white;
  font-family: 'Josefin Sans', sans-serif;
  position: absolute;
  width: 100%;
  overflow-x: hidden;
}

.feature, p {
    font-size: 20pt;
    font-family: 'Josefin Sans', sans-serif;
    text-align: left;
}

p.bottom-right {
    text-align: right;
    position: relative;
    bottom: 0px;
    font-size: 15pt;
}

#header {
    margin: 0px;
    margin-top: 45vh;
    margin-bottom: 45vh;
    text-shadow: 3px 3px 0px black;
    font-size: 48pt;
    font-family: 'Alatsi', cursive;
    transition: 2s all;
}

.down-arrow {
    color: white;
    text-shadow: 1px 1px 0px black;
    font-size: 24pt;
    background: none;
    border: none;
    margin: 0;
    padding: 0;
    line-height: 10px;
}

.down-arrow p {
    font-size: 15pt;
    text-align: center;
    font-family: Mali, sans-serif;
    font-style: italic;
}

figure.parallax {
    position: absolute;
    top: 0px;
    margin: 0;
    overflow: hidden;
    height: 100%;
    width: 100%;
    z-index: 0;
    pointer-events: none;
}

.stars {
    background-repeat: repeat;
    height: 1000vh;
    width: 100vw;
}

.stars-front {
    background-image: url("./img/stars1.png");
}

.stars-back {
    background-image: url("./img/stars2.png");
}

.trees {
    background-image: url("./img/beach.png");
    width: 100vw;
    max-width: 100%;
    height: 30vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    position: relative;
    bottom: 0px;
    z-index: 1;
    position: absolute;
    bottom: 0px;
}

.cloud-parallax-left {
    position: absolute;
    bottom: 0px;
    height: 100%;
}

.cloud-parallax-right {
    position: absolute;
    bottom: 0px;
    right: 0px;
    height: 100%;
}

.cloud-1 {
    background-image: url("./img/clouds 1.png");
    width: 50vh;
    height: 20vh;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    top: 500px;
    left: 00px;
}

.cloud-2 {
    background-image: url("./img/clouds 2.png");
    width: 30vh;
    height: 20vh;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    top: 700px;
    right: 200px;
}

.cloud-3 {
    background-image: url("./img/clouds 2.png");
    width: 60vh;
    height: 30vh;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    top: 300px;
    right: 1200px;
}

.two-sections {
    display: flex;
}

.one-section h1 {
    padding-bottom: 1.5em;
}

.center-vertically {
    margin: auto;
    position: relative;
    z-index: 100;
}

.right-align, .feature.right-align, .feature.right-align p{
    text-align: right;
}

@keyframes bob-in-place {
    0% {
        top: -10px;
    }
    50% {
        top: 10px;
    }
    100% {
        top: -10px;
    }
}

.mushroom {
    background-image: url("./img/mushroom.png");
    width: 400px;
    height: 620px;
    background-size: contain;
    flex-shrink: 0;
    position: relative;
    animation: bob-in-place 10s infinite;
}

.bird-1 {
    background-image: url("./img/bird1.png");
    width: 500px;
    height: 400px;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    animation: bob-in-place 10s infinite;
}

.github {
    background-image: url('./img/github.png');
    width: 500px;
    height: 400px;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    animation: bob-in-place 15s infinite;
}

.feature {
    min-height: 400px;
    padding-top: 20vh;
    padding-bottom: 20vh;
    padding-left: 15%;
    padding-right: 15%;
}

.feature h1 {
    font-family: 'Alatsi', sans-serif;
    font-weight: normal;
    text-shadow: 3px 3px 0px black;
}

.react-photo-gallery--gallery>div {
    justify-content: center;
}

.react-photo-gallery--gallery img {
    border-radius: 3px;
}

.react-photo-gallery--gallery {
    position: relative;
    z-index: 100;
}

.link-button {
    position: relative;
    z-index: 1;
    padding: 7px 20px;
    background-color: white;
    color: black;
    box-shadow: 3px 3px 0px black;
    text-decoration:none;
    font-weight: bold;
    transition: .5s all;
    text-shadow: none;
}

.link-button-big {
    padding: 40px 80px;
    font-size: 48pt;
}

.link-button:hover{
    background-color: #fff2ce;
}

.contact-info {

}

section {
    z-index: -10;
    position: relative;
    top: 0px;
}

section.section-short {
    height: 400px;
}

section.js-color-stop {
        z-index: 10;
    min-height: 100vh;
}
